@import url(https://fonts.googleapis.com/css?family=Bungee+Shade);
@import url(https://fonts.googleapis.com/css?family=Fredoka+One);

.characters-page {
  width: 80%;
  margin: 0px auto;
  padding: 0 0 2em 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;

}

.character-card {
  width: 18%;
  border: 1px solid white;
  margin: 2em auto auto;
}

.character-img {
  width: 100%;
}

.character-name {
  background: white;
  margin: 0;
  padding: 0;
  text-align: center;
}

.pagination {
  width: auto;
  height: 30px;
  padding: 0 0 0 60px;
}

.currentPage {
  background: red;
}

.pagination-button {
  background: white;
}

.hide {
  display: none;
}


.link { 
  width: 100px;
  height: 100px;
  background: red;
}

.link-selected {
  background: yellow;
}

.stars {
  background: url(http://www.script-tutorials.com/demos/360/images/stars.png) top center #000;
}

.twinkling {
  background: url(http://www.script-tutorials.com/demos/360/images/twinkling.png) top center;
  -webkit-animation-name: move-twink-back;
          animation-name: move-twink-back;
  -webkit-animation-duration: 200s;
          animation-duration: 200s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-fill-mode: none;
          animation-fill-mode: none;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.stars, .twinkling {
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
}

.stars, .twinkling {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.logo {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-decoration: none;
}

.title { 
  font-family: 'Bungee Shade';
  font-size: 4em;
}

.subtitle {
  font-family: 'Fredoka One';
  font-size: .75em;
  color: #FFF;
  letter-spacing: 1em;
  margin: 2px 0;
}

.title-color {
  color: #FFEE58!important;
}

.home-page {
  width: 80%;
  margin:  50px auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.category {
  margin-top: 2em;
  margin-bottom: 2em;
  width: 31.76157%;
}

.category:nth-child(3n+1) {
  margin-right: 10px;
}

.category:nth-child(3n+2) {
  margin-right: 10px;
  }

.link {
  text-decoration: none;
  color: #FFEE58!important;
  font-family: 'Fredoka One';
}

.card {
  width: 100%;
}

.card-title {
  padding-left: 1em;
  padding-right: 15.23em;
  background-color: rgba(0,0,0,.4);
  position: relative;
  bottom: 40px;
  text-transform: uppercase;
}

.card-img {
  width: 100%;
}

.card-details {
  width: 80%;
  border: 1px solid white;
  background: white;
  opacity: 0.8;
  margin: 50px auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.left-block {
  width: 35%;

}

.right-block {
  width: 75%;
  padding: 90px;
}




@-webkit-keyframes move-twink-back{
  from{background-position:0 0}
  to{background-position:-10000px 5000px}
}




@keyframes move-twink-back{
  from{background-position:0 0}
  to{background-position:-10000px 5000px}
}
